/*=== MEDIA QUERY ===*/
@import url("https://fonts.googleapis.com/css?family=Poppins:400,700,800|Source+Sans+Pro:400|Catamaran:400,600,700");
html, body {
  height: 100%;
}

body {
  line-height: 1.7;
  font-family: "Catamaran", sans-serif;
  font-size: 16px;
  -webkit-font-smoothing: antialiased;
}

p {
  font-family: "Source Sans Pro", sans-serif;
  color: #757575;
}

h1, h2, h3, h4, h5, h6, .h1, .h2, .h3, .h4, .h5, .h6 {
  font-family: "Catamaran", sans-serif;
  font-weight: 600;
  color: #000;
}

h4, .h4 {
  font-size: 22px;
}

h5, .h5 {
  font-size: 18px;
}

h6, .h6 {
  font-size: 16px;
}

.wrapper {
  padding: 0px 100px;
}

a,
button {
  -webkit-transition: all .3s;
  transition: all .3s;
}

a:hover,
a:focus,
a button:hover,
button:hover,
button:focus,
button button:hover {
  -webkit-transition: all .3s;
  transition: all .3s;
  text-decoration: none;
  outline: none;
  box-shadow: none;
}

a,
a:focus,
a:hover {
  text-decoration: none;
  outline: 0;
  color: #fb5c42;
}

.btn.active:focus,
.btn:active:focus,
.btn:focus {
  outline: 0;
  box-shadow: none !important;
}

.btn.focus,
.btn:focus {
  box-shadow: none !important;
}

.navbar-toggle .icon-bar {
  background: #fb5c42;
}

input[type="email"],
input[type="password"],
input[type="text"],
input[type="tel"] {
  border-radius: 0;
  box-shadow: none;
  height: 45px;
  background: #eee;
  border-color: #eee;
  outline: none;
}

input[type="email"]:focus,
input[type="password"]:focus,
input[type="text"]:focus,
input[type="tel"]:focus {
  box-shadow: none;
  border: 1px solid #fb5c42;
}

.form-control {
  box-shadow: none;
  border-radius: 0;
  height: 45px;
}

.form-control:focus {
  box-shadow: none;
  border: 1px solid #fb5c42;
}

.btn {
  font-size: 18px;
  letter-spacing: .5px;
  padding: 14px 35px;
  text-transform: capitalize;
  font-weight: 600;
  border-radius: 0px;
  border: 2px solid transparent;
}

.btn-main {
  background: #fb5c42;
  color: #fff !important;
  border-color: #fb5c42;
}

.btn-main:hover {
  background: transparent;
  border-color: #fb5c42;
  color: #000 !important;
}

.btn-main-transparent {
  background: transparent;
  border: 2px solid #fb5c42;
  color: #000;
}

.btn-main-transparent:hover {
  background: #fb5c42;
  color: #fff;
}

.btn-solid-border {
  border: 2px solid #000;
  background: #000;
  color: #fff;
}

.btn-solid-border:hover {
  background: #fb5c42;
  color: #fff !important;
  border-color: #fb5c42;
}

.btn-transparent {
  background: transparent;
  color: #000 !important;
  border: 2px solid #000;
}

.btn-transparent:hover {
  background: #fb5c42;
  color: #fff !important;
  border-color: #fb5c42;
}

.btn-black {
  background: #000;
  color: #fff;
  border-color: #000;
}

.btn-black:hover {
  background: #fb5c42;
  color: #fff;
  border-color: #fb5c42;
}

.btn-small {
  padding: 10px 25px;
}

.btn-extra-small {
  padding: 5px 10px;
  font-size: 12px;
}

.btn.active:focus,
.btn:active:focus,
.btn:focus {
  outline: 0;
  box-shadow: none;
}

.btn:focus {
  color: #ddd;
}

.bg-gray {
  background: #f9f9f9;
}

.section {
  padding: 80px 0;
}

.title {
  margin-bottom: 60px;
}

.title h2 {
  font-size: 38px;
  line-height: 48px;
  margin-bottom: 0px;
  font-family: "Poppins", sans-serif;
  text-transform: capitalize;
}

.social-media-icons ul li {
  display: inline-block;
}

.social-media-icons ul li a {
  font-size: 18px;
  color: #333;
  display: inline-block;
  padding: 7px 12px;
  color: #fff;
}

.social-media-icons ul li .twitter {
  background: #00aced;
}

.social-media-icons ul li .facebook {
  background: #3b5998;
  padding: 7px 18px;
}

.social-media-icons ul li .googleplus {
  background: #dd4b39;
}

.social-media-icons ul li .dribbble {
  background: #ea4c89;
}

.social-media-icons ul li .instagram {
  background: #bc2a8d;
}

.text-gray {
  color: #898a8f;
}

.bg-gray {
  background: #f2f3f5;
}

.mb-40 {
  margin-bottom: 35px;
}

.read-more {
  text-transform: uppercase;
  font-size: 14px;
  color: #000;
  border-bottom: 1px solid #000;
  font-weight: 600;
}

.read-more:hover {
  color: #fb5c42 !important;
  border-color: #fb5c42;
}

.pagination {
  justify-content: center;
}

.pagination .page-item .page-link {
  display: inline-block;
  width: 40px;
  height: 40px;
  border-radius: 0;
  border: 1px solid #dedede;
  text-align: center;
  margin-right: 6px;
  color: #757575;
}

.pagination .page-item .page-link:hover {
  background: #fb5c42;
  color: #fff;
}

.pagination .page-item:first-child .page-link, .pagination .page-item:last-child .page-link {
  border-radius: 0;
}

.pagination .page-item.active .page-link {
  background: #fb5c42;
  color: #fff;
  border-color: #fb5c42;
}

.text-primary {
  color: #fb5c42 !important;
}

a.text-primary:focus,
a.text-primary:hover {
  color: #fb5c42 !important;
}

.content * {
  margin-bottom: 20px;
}

.content h1,
.content h2,
.content h3,
.content h4,
.content h5,
.content h6 {
  margin-bottom: 10px;
}

.content ol {
  padding-left: 15px;
}

.content ul {
  padding-left: 0;
}

.content ul li {
  position: relative;
  list-style-type: none;
  padding-left: 15px;
  margin-bottom: 10px;
}

.content ul li::before {
  position: absolute;
  content: "\f363";
  font-family: "themefisher-font";
  font-size: 20px;
  left: 0;
  top: -3px;
  color: #fb5c42;
  transition: .3s ease;
}

.content table {
  text-align: left;
  width: 100%;
  max-width: 100%;
  margin-bottom: 1rem;
  border: 1px solid #dee2e6;
}

.content table th,
.content table td {
  padding: .75rem;
  vertical-align: top;
  border: 1px solid #dee2e6;
}

.content table thead {
  background: #707070;
}

.content table tbody {
  background: #757575;
}

.content table tbody td {
  text-align: left !important;
}

.content blockquote {
  padding: 20px;
  border-left: 5px solid #fb5c42;
}

.content blockquote p {
  margin-bottom: 0;
  color: #000;
  font-style: italic !important;
}

.content pre {
  padding: 10px 20px;
  background: #757575;
}

.page-header {
  background: #f5f5f5;
  background: url("../images/backgrounds/header-bg.jpg") no-repeat 50% 50%;
  background-size: cover;
  padding: 60px 0px;
  position: relative;
}

.page-header h1 {
  margin-bottom: 0px;
}

.page-header p {
  color: #000;
}

.page-wrapper {
  padding: 70px 0;
}

.search-wrap {
  position: relative;
  z-index: 1200;
  top: 20px;
  right: 16px;
  display: inline-block;
  float: right;
}

.overlay.open {
  visibility: visible;
  height: 100%;
  opacity: 1;
}

.overlay {
  position: fixed;
  top: 0;
  left: 0;
  visibility: hidden;
  overflow: hidden;
  width: 100%;
  height: 0;
  -webkit-transition: opacity .35s, visibility .35s, height .4s;
  transition: opacity .35s, visibility .35s, height .4s;
  opacity: 0;
  background: rgba(0, 0, 0, 0.98);
}

.overlay .search-form {
  position: relative;
  height: 100%;
  padding-top: 300px;
}

.overlay .search-form input {
  font-size: 56px;
  height: 60px;
  color: #fff;
  border: none;
  background: transparent;
}

.search_toggle {
  cursor: pointer;
}

.toggle-wrap.active {
  position: relative;
  z-index: 10000;
  top: 0;
  right: 0;
  cursor: pointer;
  -webkit-transition: opacity .25s ease;
  transition: opacity .25s ease;
}

.toggle-wrap.active img.search-close {
  display: block;
}

.navigation {
  margin-bottom: 0;
  padding: 15px 0px;
}

.navigation .navbar-nav > li {
  padding: 0px 10px;
}

.navigation .navbar-nav > li.active a {
  color: #000;
}

.navigation .navbar-nav > li > a {
  color: #000;
  font-size: 17px;
  padding: 20px 15px;
  text-transform: capitalize;
  transition: .2s ease-in-out 0s;
  letter-spacing: .5px;
  font-weight: 600;
}

.navigation .navbar-nav > li > a:hover, .navigation .navbar-nav > li > a:active, .navigation .navbar-nav > li > a:focus {
  background: none;
  color: #fb5c42;
}

.navigation .navbar-nav .nav-link {
  color: #000;
}

.navigation .navbar-nav .nav-link:hover {
  color: #fb5c42;
}

.navigation .navbar-nav .nav-link span {
  font-size: 8px;
  margin-left: 3px;
}

.navigation .nav .open > a {
  border: 1px solid transparent;
  background-color: transparent;
}

.navigation .dropdown-slide {
  -webkit-transition: all .35s ease;
  -moz-transition: all .35s ease;
  -ms-transition: all .35s ease;
  -o-transition: all .35s ease;
  transition: all .35s ease;
}

.navigation .dropdown-slide .dropdown-menu {
  border: none;
  display: block;
  position: absolute;
  width: 200px;
  border-top: 3px solid #fb5c42;
  left: auto;
  padding: 10px;
  border-radius: 0px;
  opacity: 0;
  visibility: hidden;
  box-shadow: 0px 1px 7px 0px rgba(0, 0, 0, 0.1);
  -webkit-transition: all .35s ease;
  -moz-transition: all .35s ease;
  -ms-transition: all .35s ease;
  -o-transition: all .35s ease;
  transition: all .35s ease;
  margin-top: 20px;
}

.navigation .dropdown-slide .dropdown-menu li a {
  color: #222;
  font-size: 15px;
  border: 1px solid transparent;
  display: block;
  padding: 8px 16px;
  letter-spacing: 0.5px;
  text-transform: capitalize;
  -webkit-transition: .3s all;
  -o-transition: .3s all;
  transition: .3s all;
}

.navigation .dropdown-slide .dropdown-menu li a:hover {
  background-color: #000;
  color: #fff;
}

.navigation .dropdown-slide .cart-dropdown {
  width: 290px !important;
  padding: 35px 20px;
  right: -40px;
  -webkit-transform: translateY(25px);
  -moz-transform: translateY(25px);
  -ms-transform: translateY(25px);
  -o-transform: translateY(25px);
  transform: translateY(25px);
}

.dropdown-toggle::after {
  display: none;
}

.menu:hover {
  content: '';
  position: absolute;
  bottom: -1px;
  left: 0;
  width: 100%;
  height: 1px;
  transition: transform 0.3s cubic-bezier(0.8, 0, 0.2, 1);
  transform: scale3d(0, 1, 1);
  transform-origin: 100% 50%;
  background-color: #fff;
}

.top-menu li .cart-icon {
  position: relative;
}

.top-menu li a {
  color: #000;
}

.top-menu li a i {
  width: 35px;
  height: 35px;
  background: #eee;
  display: inline-block;
  color: #000;
  text-align: center;
  padding-top: 10px;
  border-radius: 100%;
}

.top-menu li span {
  font-weight: 600;
}

.cart-dropdown {
  box-shadow: 0px 1px 7px 0px rgba(0, 0, 0, 0.1);
  padding: 30px 0px;
}

.cart-dropdown .media {
  position: relative;
  border-bottom: 1px solid #dedede;
  padding-bottom: 15px;
  margin-bottom: 10px;
}

.cart-dropdown .media h6 {
  font-weight: 400;
}

.cart-dropdown img {
  width: 60px;
}

.cart-dropdown h4 {
  color: #000;
  font-weight: 300;
  font-size: 14px;
}

.cart-dropdown .cart-price {
  color: #7f7f7f;
  font-size: 12px;
  font-weight: 200;
}

.cart-dropdown .remove {
  padding: 1px 6px;
  position: absolute;
  right: 0;
  top: 0;
  color: #000 !important;
  font-size: 12px;
}

.cart-summary {
  margin-top: 10px;
  font-weight: 500;
  color: #000;
  font-size: 14px;
}

.cart-summary .total-price {
  float: right;
}

.nav-item.dropdown.dropdown-slide:hover .dropdown-menu {
  opacity: 1;
  visibility: visible;
  margin-top: -5px;
  z-index: 9999;
}

.dropdown.cart-nav.dropdown-slide:hover .cart-dropdown {
  display: block;
  opacity: 1;
  z-index: 9999;
  visibility: visible;
  margin-top: -5px;
}

@media (max-width: 480px) {
  .navigation {
    padding: 15px 25px;
  }
  .navigation .navbar-nav .nav-link {
    border-bottom: 1px solid #dedede;
    padding: 10px 0px;
  }
}

@media (max-width: 400px) {
  .navigation {
    padding: 15px 25px;
  }
  .navigation .navbar-nav .nav-link {
    border-bottom: 1px solid #dedede;
    padding: 10px 0px;
  }
}

@media (max-width: 768px) {
  .navigation {
    padding: 15px 25px;
  }
  .navigation .navbar-nav .nav-link {
    border-bottom: 1px solid #dedede;
    padding: 10px 0px;
  }
}

@media (max-width: 992px) {
  .navigation {
    padding: 15px 25px;
  }
  .navigation .navbar-nav .nav-link {
    border-bottom: 1px solid #dedede;
    padding: 10px 0px;
  }
}

.navbar-nav .nav-link {
  color: #000;
}

.navbar-nav .nav-link:hover {
  color: #fb5c42;
}

.navbar-nav .nav-link span {
  font-size: 8px;
  margin-left: 3px;
}

.navbar-nav .nav-link .nav .open > a {
  border: 1px solid transparent;
  background-color: transparent;
}

.slider {
  position: relative;
}

.slider .slider-item {
  background-size: cover;
}

.slider .slider-caption {
  padding: 160px 0px;
}

.slider .slider-caption span {
  opacity: .85;
  text-transform: capitalize;
}

.slider .slider-caption h1 {
  font-weight: 800;
  font-family: "Poppins", sans-serif;
  font-size: 90px;
  line-height: 90px;
}

.slider .btn-main {
  position: relative;
  z-index: 2;
  -webkit-transition: all .35s ease-in-out;
  -moz-transition: all .35s ease-in-out;
  -ms-transition: all .35s ease-in-out;
  -o-transition: all .35s ease-in-out;
  transition: all .35s ease-in-out;
}

.slider .btn-main:before {
  position: absolute;
  content: "";
  left: -10px;
  top: -10px;
  border: 2px solid #fb5c42;
  width: 100%;
  height: 100%;
  -webkit-transition: all .25s ease-in-out;
  -moz-transition: all .25s ease-in-out;
  -ms-transition: all .25s ease-in-out;
  -o-transition: all .25s ease-in-out;
  transition: all .25s ease-in-out;
}

.slider .btn-main:hover {
  background: #000;
  border-color: #000;
  color: #fff !important;
}

.slider .btn-main:hover:before {
  border-color: #000;
}

.slick-control-prev i {
  font-size: 20px;
  width: 60px;
  height: 60px;
  background: #fb5c42;
  color: #fff;
  display: inline-block;
  text-align: center;
  padding-top: 20px;
  border-radius: 100%;
}

.slick-control-next i {
  font-size: 20px;
  width: 60px;
  height: 60px;
  background: #fb5c42;
  color: #fff;
  display: inline-block;
  text-align: center;
  padding-top: 20px;
  border-radius: 100%;
}

.slick-control-prev, .slick-control-next {
  font-size: 0;
  line-height: 0;
  position: absolute;
  top: 50%;
  display: block;
  padding: 0;
  -webkit-transform: translate(0, -50%);
  -ms-transform: translate(0, -50%);
  transform: translate(0, -50%);
  cursor: pointer;
  color: transparent;
  border: none;
  outline: none;
  background: transparent;
  z-index: 999;
  opacity: 0;
  -webkit-transition: all .25s ease-in-out;
  -moz-transition: all .25s ease-in-out;
  -ms-transition: all .25s ease-in-out;
  -o-transition: all .25s ease-in-out;
  transition: all .25s ease-in-out;
}

.slick-control-prev {
  left: 50px;
}

.slick-control-next {
  right: 50px;
}

.slider:hover .slick-control-prev,
.slider:hover .slick-control-next {
  opacity: 1;
}

.slick-dots {
  display: none !important;
}

