.product {
  position: relative;
  transition: .4s all;
  overflow: hidden;
}

.product:hover .product-hover-overlay {
  opacity: 1;
}

.product:hover .img-second {
  opacity: 1;
  -webkit-transform: scale(1);
  transform: scale(1);
}

.img-first {
  -webkit-transform: scale(1);
  transform: scale(1);
  opacity: 1;
  transition: .2s linear;
}

.img-second {
  opacity: 0;
  position: absolute;
  top: 0;
  left: 0px;
  -webkit-transform: scale(0.9);
  transform: scale(0.9);
  transition: .2s linear;
}

.product-hover-overlay {
  position: absolute;
  bottom: 120px;
  left: 0px;
  width: 100%;
  transition: .4s all;
  opacity: 0;
  display: flex;
  justify-content: center;
}

.product-hover-overlay a {
  background: #000;
  color: #fff;
  cursor: pointer;
  display: inline-block;
  font-size: 16px;
  padding-top: 13px;
  transition: .2s all;
  width: 50px;
  height: 50px;
  border-radius: 100%;
  text-align: center;
  margin: 0px 1px;
}

.product-hover-overlay a:hover {
  background: #fb5c42;
  color: #fff;
}

.product-title a {
  color: #000;
  letter-spacing: .5px;
}

.product-info {
  text-align: center;
}

.product-info .price {
  color: #777;
  font-size: 18px;
  font-family: "Source Sans Pro", sans-serif;
}

.product-info del {
  margin-right: 8px;
}

.onsale {
  background: #fb5c42;
  display: inline-block;
  color: #fff;
  position: absolute;
  top: 20px;
  right: 0px;
  padding: 5px 10px;
  text-align: center;
  font-size: 12px;
  text-transform: uppercase;
  font-weight: 600;
  letter-spacing: .5px;
}

.products-main .product-hover-overlay .btn, .products-shop .product-hover-overlay .btn {
  font-size: 14px;
  padding: 10px 29px;
  text-transform: capitalize;
  border-radius: 33px;
  font-weight: 600;
  letter-spacing: .5px;
  background: #fb5c42;
  border-color: #fb5c42;
  color: #fff;
