.wrapper {
  padding: 0px 100px;
}


a,
button {
  -webkit-transition: all .3s;
  transition: all .3s;
}

a:hover,
a:focus,
a button:hover,
button:hover,
button:focus,
button button:hover {
  -webkit-transition: all .3s;
  transition: all .3s;
  text-decoration: none;

  outline: none;
  box-shadow: none;
}


a,
a:focus,
a:hover {
  text-decoration: none;
  outline: 0;
  color: $primary-color;
}

.btn.active:focus,
.btn:active:focus,
.btn:focus {
  outline: 0;
  box-shadow: none !important;
}

.btn.focus,
.btn:focus {
  box-shadow: none !important;
}

.navbar-toggle .icon-bar {
  background: $primary-color;
}

input[type="email"],
input[type="password"],
input[type="text"],
input[type="tel"] {
  border-radius: 0;
  box-shadow: none;
  height: 45px;
  background: #eee;
  border-color: #eee;
  outline: none;

  &:focus {
    box-shadow: none;
    border: 1px solid $primary-color;
  }
}


.form-control {
  box-shadow: none;
  border-radius: 0;
  height: 45px;

  &:focus {
    box-shadow: none;
    border: 1px solid $primary-color;
  }
}



// Button Style

.btn {
  font-size: 18px;
  letter-spacing: .5px;
  padding: 14px 35px;
  text-transform: capitalize;
  font-weight: 600;
  border-radius: 0px;
  border: 2px solid transparent;
}

.btn-main {
  background: $primary-color;
  color: $white !important;
  border-color: $primary-color;

  &:hover {
    background: transparent;
    border-color: $primary-color;
    color: $black !important;
  }
}


.btn-main-transparent {
  background: transparent;
  border: 2px solid $primary-color;
  color: $black;

  &:hover {
    background: $primary-color;
    color: $white;
  }

}

.btn-solid-border {
  border: 2px solid $black;
  background: $black;
  color: $white;

  &:hover {
    background: $primary-color;
    color: $white !important;
    border-color: $primary-color;
  }
}


.btn-transparent {
  background: transparent;
  color: $black !important;
  border: 2px solid $black;

  &:hover {
    background: $primary-color;
    color: $white !important;
    border-color: $primary-color;
  }
}

.btn-black {
  background: $black;
  color: $white;
  border-color: $black;

  &:hover {
    background: $primary-color;
    color: $white;
    border-color: $primary-color;
  }

}


.btn-small {
  padding: 10px 25px;
}

.btn-extra-small {
  padding: 5px 10px;
  font-size: 12px;
}


.btn.active:focus,
.btn:active:focus,
.btn:focus {
  outline: 0;
  box-shadow: none;
}



.btn:focus {
  color: #ddd;
}



.bg-gray {
  background: #f9f9f9;
}


.section {
  padding: 80px 0;
}

.title {
  margin-bottom: 60px;

  h2 {
    font-size: 38px;
    line-height: 48px;
    margin-bottom: 0px;
    font-family: $extra-font;
    text-transform: capitalize;
  }
}



// Social Media Icons 
.social-media-icons {
  ul {
    li {
      display: inline-block;

      a {
        font-size: 18px;
        color: #333;
        display: inline-block;
        padding: 7px 12px;
        color: $white;

      }

      .twitter {
        background: #00aced;
      }

      .facebook {
        background: #3b5998;
        padding: 7px 18px;
      }

      .googleplus {
        background: #dd4b39;
      }

      .dribbble {
        background: #ea4c89;
      }

      .instagram {
        background: #bc2a8d;
      }
    }
  }
}




.text-gray {
  color: #898a8f;
}

.bg-gray {
  background: $secondary-color;
}

.mb-40 {
  margin-bottom: 35px;
}



.read-more {
  text-transform: uppercase;
  font-size: 14px;
  color: $black;
  border-bottom: 1px solid $black;
  font-weight: 600;

  &:hover {
    color: $primary-color !important;
    border-color: $primary-color;
  }
}


.pagination {
  justify-content: center;

  .page-item {
    .page-link {
      display: inline-block;
      width: 40px;
      height: 40px;
      border-radius: 0;
      border: 1px solid $border-color;
      text-align: center;
      margin-right: 6px;
      color: $light;

      &:hover {
        background: $primary-color;
        color: $white;
      }
    }

    &:first-child,
    &:last-child {
      .page-link {
        border-radius: 0;
      }
    }

    &.active {
      .page-link {
        background: $primary-color;
        color: $white;
        border-color: $primary-color;
      }
    }
  }
}

.text-primary {
  color: $primary-color !important;
}

a.text-primary:focus,
a.text-primary:hover {
  color: $primary-color !important;
}

.content {
  * {
    margin-bottom: 20px;
  }

  h1,
  h2,
  h3,
  h4,
  h5,
  h6 {
    margin-bottom: 10px;
  }

  ol{
    padding-left: 15px;
  }
  ul{
    padding-left: 0;

    li {
      position: relative;
      list-style-type: none;
      padding-left: 15px;
      margin-bottom: 10px;

      &::before {
        position: absolute;
        content: "\f363";
        font-family: $icon-font;
        font-size: 20px;
        left: 0;
        top: -3px;
        color: $primary-color;
        transition: .3s ease;
      }
    }
  }

  table {
    text-align: left;
    width: 100%;
    max-width: 100%;
    margin-bottom: 1rem;
    border: 1px solid #dee2e6;

    th,
    td {
      padding: .75rem;
      vertical-align: top;
      border: 1px solid #dee2e6
    }

    thead {
      background: darken($color: $light, $amount: 2);
    }

    tbody {
      background: $light;

      td {
        text-align: left !important;
      }
    }
  }


  blockquote {
    padding: 20px;
    border-left: 5px solid $primary-color;
    p {
      margin-bottom: 0;
      color: $black;
      font-style: italic !important;
    }
  }


  pre {
    padding: 10px 20px;
    background: $light;
  }
}